import React from 'react'
import { Tabs, Tab } from "react-bootstrap"
import { Container} from "./TabElements"
import {Retirada} from "../Cadastro/Retirada"
import {ConsultarRetirada} from "../Consulta/Index"

export const RetiradaTab = () => {
    return (
        <Container className="container">

            <Tabs defaultActiveKey="profile" id="tabs-produtos" >
                <Tab eventKey="home" title="Entrega de Produtos">
                    <Retirada />
                </Tab>

                <Tab eventKey="profile" title="Consultar Entregas">
                    <ConsultarRetirada/>
                </Tab>
            </Tabs>
        </Container>
    )
}
