import React from 'react'
import {Container} from "./RetiradaElements"
import {Col, Row} from "react-bootstrap"

export const Retirada = () => {
    return (
        <Container>
            <h1>Entregas de Produtos</h1>

            <form>
                    <Row>
                        <Col xs={12} md={4}>
                            <label htmlFor="cod_barras">Código de barras: </label>
                            <input type="number" name="cod_barras" id="cod_barras" />
                        </Col>

                        <Col xs={12} md={4}>
                            <label htmlFor="desc_produto">Produto: </label>
                            <input type="text" name="desc_produto" id="desc_produto" />
                        </Col>

                        <Col xs={12} md={4}>
                            <label htmlFor="cod_interno">Código Interno: </label>
                            <input type="number" name="cod_interno" id="cod_interno" />
                        </Col>

                        <Col xs={12} md={4}>
                            <label htmlFor="qtd">Quantidade: </label>
                            <input type="number" name="qtd" id="qtd" />
                        </Col>

                        <Col xs={12} md={4}>
                            <label htmlFor="und">Unid. Medida: </label>
                            <select name="und" id="und">
                                <option value="MT">MT</option>
                                <option value="UND">UND</option>
                                <option value="KG">KG</option>
                                <option value="LT">LT</option>
                                <option value="CX">CX</option>
                                <option value="PC">PC</option>
                                <option value="FD">FD</option>
                            </select>
                        </Col>

                        <Col xs={12} md={4}>
                            <label htmlFor="numero_placa">Nº da Placa: </label>
                            <input type="text" name="numero_placa" id="numero_placa"/>
                        </Col>

                        <Col xs={12} md={4}>
                            <label htmlFor="localizacao">Localização:</label>
                            <input type="text" name="localizacao" id="localizacao" />
                        </Col>

                        <Col xs={12} md={4}>
                            <label htmlFor="localizacao">Requisitante:</label>
                            <input type="text" name="localizacao" id="localizacao" />
                        </Col>

                    </Row>

                    <button type="submit" className="btn-submit">Salvar</button>

                </form>


        </Container>

    )
}
